export type CountryCode = (typeof COUNTRIES)[number]['code']

export function getCountryName(code: string | null | undefined): string {
  if (!code) return ''
  const country = COUNTRIES.find((c) => c.code === code || c.name.toLowerCase() === code.toLowerCase())
  return country?.name || code
}

export function getCountryCode(nameOrCode: string | null | undefined): string {
  if (!nameOrCode) return ''
  const country = COUNTRIES.find(
    (c) => c.code === nameOrCode || c.name.toLowerCase() === nameOrCode.toLowerCase()
  )
  return country?.code || nameOrCode
}

interface CountrySelectProps {
  value: string | null | undefined
  onChange: (value: string) => void
  placeholder?: string
  disabled?: boolean
  required?: boolean
  className?: string
}

export function CountrySelect({
  value,
  onChange,
  placeholder = 'Select country...',
  disabled = false,
  required = false,
  className,
}: CountrySelectProps) {
  const [open, setOpen] = React.useState(false)

  const selectedCountry = COUNTRIES.find(
    (c) => c.code === value || c.name.toLowerCase() === value?.toLowerCase()
  )

  return (
    <Popover open={open} onOpenChange={setOpen}>
      <PopoverTrigger asChild>
        <Button
          variant="outline"
          role="combobox"
          aria-expanded={open}
          aria-required={required}
          disabled={disabled}
          className={cn('w-full justify-between font-normal', className)}
        >
          {selectedCountry ? selectedCountry.name : placeholder}
          <ChevronsUpDown className="ml-2 h-4 w-4 shrink-0 opacity-50" />
        </Button>
      </PopoverTrigger>
      <PopoverContent className="w-[300px] p-0" align="start">
        <Command>
          <CommandInput placeholder="Search country..." />
          <CommandList>
            <CommandEmpty>No country found.</CommandEmpty>
            <CommandGroup className="max-h-[300px] overflow-auto">
              {COUNTRIES.map((country) => (
                <CommandItem
                  key={country.code}
                  value={country.name}
                  onSelect={() => {
                    onChange(country.code)
                    setOpen(false)
                  }}
                >
                  <Check
                    className={cn(
                      'mr-2 h-4 w-4',
                      selectedCountry?.code === country.code ? 'opacity-100' : 'opacity-0'
                    )}
                  />
                  {country.name}
                </CommandItem>
              ))}
            </CommandGroup>
          </CommandList>
        </Command>
      </PopoverContent>
    </Popover>
  )
}

// Nationality is often the same as country, but this provides a clearer label
export function NationalitySelect(props: CountrySelectProps) {
  return <CountrySelect {...props} placeholder={props.placeholder || 'Select nationality...'} />
}
